/**
 * PeerManager 
 * PeerJS  P2P  
 *
 * : 2025-11-13
 * : Peer ,  ,  
 */

import Peer, { DataConnection } from 'peerjs';
import { Message } from './protocol';

/**
 * Peer   
 */
export interface PeerManagerEvents {
  open: (peerId: string) => void;          // Peer  
  connection: (conn: DataConnection) => void; //   
  data: (data: Message, conn: DataConnection) => void; //  
  close: (conn: DataConnection) => void;   //  
  error: (error: Error) => void;           //  
  disconnected: () => void;                // Peer   
}

/**
 * PeerManager 
 * P2P     
 */
export class PeerManager {
  private peer: Peer | null = null;        // Peer 
  private connections: Map<string, DataConnection> = new Map(); //   (peerId -> connection)
  private eventHandlers: Map<keyof PeerManagerEvents, Array<any>> = new Map(); //    (  )
  private reconnectAttempts: number = 0;   //   
  private maxReconnectAttempts: number = 5; //    

  /**
   * Peer  ()
   * @param customId   ID ()
   * @returns Promise<string>  Peer ID
   */
  async createPeer(customId?: string): Promise<string> {
    return new Promise((resolve, reject) => {
      try {
        // Peer  
        //   PeerJS   (0.peerjs.com -  )
        this.peer = new Peer(customId, {
          host: '0.peerjs.com',
          port: 443,
          path: '/',
          secure: true,
          config: {
            // STUN   (NAT )
            iceServers: [
              { urls: 'stun:stun.l.google.com:19302' },
              { urls: 'stun:stun1.l.google.com:19302' }
            ]
          }
        });

        // Peer   
        this.peer.on('open', (id: string) => {
          console.log('[PeerManager] Peer  :', id);
          this.reconnectAttempts = 0; //   
          this.emit('open', id);
          resolve(id);
        });

        //     ()
        this.peer.on('connection', (conn: DataConnection) => {
          console.log('[PeerManager]   :', conn.peer);
          this.handleConnection(conn);
          this.emit('connection', conn);
        });

        //   
        this.peer.on('disconnected', () => {
          console.warn('[PeerManager] Peer   ');
          this.emit('disconnected');
          this.attemptReconnect();
        });

        //  
        this.peer.on('error', (error: Error) => {
          console.error('[PeerManager] Peer :', error);
          this.emit('error', error);

          //    reject
          if (error.message.includes('ID is taken')) {
            reject(new Error('    .'));
          }
        });

      } catch (error) {
        reject(error);
      }
    });
  }

  /**
   *   ()
   * @param hostId  Peer ID
   * @returns Promise<DataConnection>  
   */
  async connectToHost(hostId: string): Promise<DataConnection> {
    return new Promise((resolve, reject) => {
      if (!this.peer) {
        reject(new Error('Peer  . createPeer()  .'));
        return;
      }

      try {
        //  
        const conn = this.peer.connect(hostId, {
          reliable: true, //    ( , )
          serialization: 'json' // JSON 
        });

        //   
        conn.on('open', () => {
          console.log('[PeerManager]   :', hostId);
          this.handleConnection(conn);
          resolve(conn);
        });

        //   
        conn.on('error', (error: Error) => {
          console.error('[PeerManager]  :', error);
          reject(error);
        });

        //   (10)
        setTimeout(() => {
          if (!conn.open) {
            reject(new Error('  '));
          }
        }, 10000);

      } catch (error) {
        reject(error);
      }
    });
  }

  /**
   *   ()
   * @param conn  
   */
  private handleConnection(conn: DataConnection): void {
    //   
    this.connections.set(conn.peer, conn);

    //   
    conn.on('data', (data: any) => {
      try {
        const message = data as Message;
        console.log('[PeerManager]  :', message.type, 'from', conn.peer);
        this.emit('data', message, conn);
      } catch (error) {
        console.error('[PeerManager]   :', error);
      }
    });

    //   
    conn.on('close', () => {
      console.log('[PeerManager]  :', conn.peer);
      this.connections.delete(conn.peer);
      this.emit('close', conn);
    });

    //  
    conn.on('error', (error: Error) => {
      console.error('[PeerManager]  :', error);
      this.emit('error', error);
    });
  }

  /**
   *   ( )
   * @param peerId  Peer ID
   * @param message  
   */
  send(peerId: string, message: Message): void {
    const conn = this.connections.get(peerId);
    if (conn && conn.open) {
      try {
        conn.send(message);
        console.log('[PeerManager]  :', message.type, 'to', peerId);
      } catch (error) {
        console.error('[PeerManager]   :', error);
      }
    } else {
      console.warn('[PeerManager]    :', peerId);
    }
  }

  /**
   *   ( )
   * @param message  
   * @param excludePeerId  Peer ID ()
   */
  broadcast(message: Message, excludePeerId?: string): void {
    console.log('[PeerManager]  :', message.type, '(', this.connections.size, ')');

    this.connections.forEach((conn, peerId) => {
      if (peerId !== excludePeerId && conn.open) {
        try {
          conn.send(message);
        } catch (error) {
          console.error('[PeerManager]  :', peerId, error);
        }
      }
    });
  }

  /**
   *   
   * @param peerId  Peer ID
   */
  disconnect(peerId: string): void {
    const conn = this.connections.get(peerId);
    if (conn) {
      conn.close();
      this.connections.delete(peerId);
      console.log('[PeerManager]  :', peerId);
    }
  }

  /**
   *   
   */
  disconnectAll(): void {
    console.log('[PeerManager]   ');
    this.connections.forEach((conn) => {
      conn.close();
    });
    this.connections.clear();
  }

  /**
   * Peer 
   */
  destroy(): void {
    console.log('[PeerManager] Peer ');
    this.disconnectAll();

    if (this.peer) {
      this.peer.destroy();
      this.peer = null;
    }

    this.eventHandlers.clear();
  }

  /**
   *  
   */
  private attemptReconnect(): void {
    if (this.reconnectAttempts >= this.maxReconnectAttempts) {
      console.error('[PeerManager]     ');
      this.emit('error', new Error('  '));
      return;
    }

    this.reconnectAttempts++;
    console.log(`[PeerManager]   (${this.reconnectAttempts}/${this.maxReconnectAttempts})`);

    if (this.peer && !this.peer.destroyed) {
      setTimeout(() => {
        this.peer?.reconnect();
      }, 1000 * this.reconnectAttempts); //  
    }
  }

  /**
   *    (  )
   * @param event  
   * @param handler  
   */
  on<K extends keyof PeerManagerEvents>(
    event: K,
    handler: PeerManagerEvents[K]
  ): void {
    if (!this.eventHandlers.has(event)) {
      this.eventHandlers.set(event, []);
    }
    this.eventHandlers.get(event)!.push(handler);
  }

  /**
   *   (  )
   * @param event  
   * @param args 
   */
  private emit<K extends keyof PeerManagerEvents>(
    event: K,
    ...args: Parameters<PeerManagerEvents[K]>
  ): void {
    const handlers = this.eventHandlers.get(event);
    if (handlers) {
      handlers.forEach(handler => {
        try {
          (handler as any)(...args);
        } catch (error) {
          console.error(`[PeerManager]    (${event}):`, error);
        }
      });
    }
  }

  /**
   * Peer ID 
   * @returns Peer ID  null
   */
  getPeerId(): string | null {
    return this.peer?.id || null;
  }

  /**
   *   
   * @returns   
   */
  getConnectionCount(): number {
    return this.connections.size;
  }

  /**
   *  Peer ID  
   * @returns Peer ID 
   */
  getConnectedPeerIds(): string[] {
    return Array.from(this.connections.keys());
  }

  /**
   *  Peer   
   * @param peerId Peer ID
   * @returns  
   */
  isConnected(peerId: string): boolean {
    const conn = this.connections.get(peerId);
    return conn ? conn.open : false;
  }
}
